<template>
  <div @click=populatePost>{{ JSON.stringify( postData )}}</div>
</template>

<script>
export default {
  name: 'post',

  data: () => ({
    
    postData: {
      markdownContent: '',
      postTitle: '',
      postViews: 0,
      postAppreciations: 0,
      postKeywords: [],
      
      owner: {
        profileAsset: {
          path: ''
        },
        userName: '',
        name: '',
        description: '',
        privilegeStatus: ''
      }
    }

  }),

  methods: {
    populatePost() {
      
      this.$store.dispatch( 'populatePost', {
        userName: this.$route.params.username,
        postId: this.$route.params.postid

      }).then( postData => {
        
        this.postData = postData;
      })
    }
  }
}
</script>

<style>
</style>



